<template>
    <div>
        <server-info v-if="currentStep === steps.findIndex(step=>step.key === 'serverInfo')"/>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="handleBusiness"
        />

		<inputCar v-if="currentStep === steps.findIndex(step=>step.key === 'inputCar')" :nextFunction="inputCarInfo" :lastInput="lastInput"/>
        <text-info v-if="currentStep === steps.findIndex(step=>step.key === 'textInfo')"/>
        <check-phone v-if="currentStep === steps.findIndex(step=>step.key === 'checkPhone')"/>
        <complete
            v-if="currentStep === steps.findIndex(step=>step.key === 'complete')"
            :data="vehicleContactResult"
            page-title="变更完成"
            :foot-texts="footTexts"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import serverInfo from '@/components/server-info';
import textInfo from './components/text-info';
import checkPhone from './components/check-phone';
import complete from '@/components/complete';
import inputCar from '@/view/pageComponents/input-car';
import process from '@/components/process';
import { vehicleCarDetail, vehicleFaceSuccess } from '@/service/traffic/traffic';
import { LOCATION_ID } from '@/constant/constant';

export default {
    name: 'VehicleContactIndex',
    components: {
        inputCar,
        identifyCardPage,
        faceYitu,
        contrast,
        serverInfo,
        textInfo,
        checkPhone,
        complete,
        process
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            visibleTip: 'visibleTip',
            visibleSteps: 'visibleSteps',
            binocularCamera: 'binocularCamera'
        }),
        ...mapState('vehicle', {
            vehicleContactResult: 'vehicleContactResult'
        })
    },
    data () {
        return {
            footTexts: '温馨提示：您的联系方式已变更成功',
            lastInput: false
        };
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage']),
        ...mapMutations('vehicle', ['setVehicleContactInfo']),
        getCar () {
            this.changeStep('down');
        },
        async handleBusiness () {
            const res = await vehicleFaceSuccess({
                identifiedPhoto: this.binocularCamera
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async inputCarInfo (params) {
            const res = await vehicleCarDetail({
                plateType: params.carType,
                plateNumber: params.carLicense,
                clsbdm: params.clsbdm
            });
            if (res && res.code === '0') {
                this.setVehicleContactInfo(res.result);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message, imgName: 'imgCar', enableBack: true });
            }
        }
    },
    mounted () {
        if (this.CONSTANT.location === LOCATION_ID.yinchuan || this.CONSTANT.location === LOCATION_ID.zhongwei) {
            this.lastInput = true;
        }
        this.setSteps([
            { name: '业务须知', isActive: true, visible: true, key: 'serverInfo' },
            { name: '身份识别', isActive: false, visible: true, key: 'identifyCardPage' },
            { name: '身份识别', isActive: false, visible: false, key: 'faceYitu' },
            { name: '身份识别', isActive: false, visible: false, key: 'contrast' },
            { name: '身份识别', isActive: false, visible: false, key: 'process' },
            { name: '输入车牌', isActive: false, visible: true, key: 'inputCar' },
            { name: '联系方式变更', isActive: false, visible: true, key: 'textInfo' },
            { name: '联系方式变更', isActive: false, visible: false, key: 'checkPhone' },
            { name: '完成提交', isActive: false, visible: true, key: 'complete' }
        ]);
    }
};

</script>
