<template>
    <div>
        <div class="kd-page-title">输入机动车信息</div>
        <div class="kd-page-content-wrapper wrap-info">
            <car-input :carTypeOptions="carTypeOptions" :get-car-input="getCarInput" :last-input="lastInput"/>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
            <kd-button icon="kedaIcon-circleright" text="下一步" :button-handle="nextStep" :but-disable="!carLicense || (lastInput && !carEngineCode)"/>
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex';
import carInput from '@/components/car-input';
import { CAR_TYPE_OPTIONS } from '@/constant/constant';

export default {
    name: 'input-car',
    components: { carInput },
    data () {
        return {
            carType: '02',
            carLicense: '',
            carEngineCode: ''
        };
    },
    props: {
        nextFunction: { type: Function, default: function () { this.changeStep('down'); } },
        lastInput: { type: Boolean, default: false },
        carTypeOptions: { type: Array, default: () => CAR_TYPE_OPTIONS }
    },
    methods: {
        ...mapMutations('common', ['changeStep']),
        getCarInput (data) {
            if (data.carType !== undefined) {
                this.carType = data.carType;
            }
            if (data.carLicense !== undefined) {
                this.carLicense = data.carLicense;
            }
            if (data.carEngineCode !== undefined) {
                this.carEngineCode = data.carEngineCode;
            }
        },
        preStep () {
            this.changeStep('up');
        },
        async nextStep () {
            await this.nextFunction({
                carType: this.carType,
                carLicense: this.carLicense,
                clsbdm: this.carEngineCode
            });
        }
    }

};
</script>

<style lang="scss" scoped>
</style>
