<template>
	<div>
		<div class="kd-page-title">
			手机修改
		</div>
		<div class="kd-page-content-wrapper wrap-info">
			<verify-code
					@getVerifyCode="getVerifyCode"
					ref="verifyCode"
					:change-phone-business="true"
					:only-need-phone="checkNeedVerify()"
			/>
		</div>
		<div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
			<kd-button
					icon="kedaIcon-policework"
					text="返回首页"
					type-class="home"
			/>
			<kd-button
					icon="kedaIcon-circleleft"
					text="上一步"
					:button-handle="goUp"
			/>
			<kd-button
					icon="kedaIcon-circleright"
					text="提交"
					:button-handle="next"
			/>
		</div>
		<alert-msg
				v-if="alertOn"
				:alertSure="sure"
				:alertCancel="cancel"
				:tip-text="tipText"
		/>
	</div>
</template>

<script>
import { mapMutations } from 'vuex';
import { trafficVerifyCode, trafficGetVerify, vehicleSubModify } from '@/service/traffic/traffic';
import alertMsg from '@/components/alert-msg';
import verifyCode from '@/components/verify-code';
import { checkNeedVerify } from '@/utils/regional';
// import audioCar from '@/assets/media/trafic/301.mp3';
// import {playAudio} from '@/utils/media';

export default {
    name: 'VehicleContactCheckPhone',
    components: {
        alertMsg,
        verifyCode
    },
    // mounted () {
    // 	this.audioCar = playAudio(audioCar);
    // 	this.audioCar.play();
    // },
    // beforeDestroy () {
    // 	this.audioCar.destroy();
    // },
    data () {
        return {
            alertOn: false,
            tipText: '确定提交机动车的联系方式吗？'
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('vehicle', ['setVehicleContactResult']),

        checkNeedVerify () {
            return checkNeedVerify(this.CONSTANT.location);
        },
        /**
			 * 检验失败提示错误信息，成功弹出确认弹框
			 */
        async next () {
            if (this.$refs.verifyCode.submission()) {
                this.tipText = '确定提交机动车的联系方式' + this.$refs.verifyCode.item.phoneNumber + '吗？';
                if (this.checkNeedVerify()) {
                    this.alertOn = true;
                } else {
                    const res = await trafficVerifyCode({
                        phone: this.$refs.verifyCode.item.phoneNumber,
                        code: this.$refs.verifyCode.item.verifyCode,
                        serviceType: 2
                    });
                    if (res && res.code === '0') {
                        this.alertOn = true;
                    } else if (res) {
                        this.changeTipPage({ message: res.message, enableBack: true });
                        this.alertOn = false;
                    }
                }
            }
        },
        /**
			 * 确认提交信息
			 */
        async sure () {
            const res = await vehicleSubModify({
                mobile: this.$refs.verifyCode.item.phoneNumber
            });
            if (res && res.code === '0') {
                this.setVehicleContactResult(res.result);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({
                    message: res.message,
                    enableBack: true
                });
            }
        },
        /**
			 * 取消隐藏提示弹框
			 */
        cancel () {
            this.alertOn = false;
        },
        async getVerifyCode () {
            const res = await trafficGetVerify({
                phone: this.$refs.verifyCode.item.phoneNumber,
                serviceType: 2
            });
            if (res && res.code === '0') {
                console.log('获取验证码成功');
            } else if (res) {
                this.changeTipPage({
                    message: res.message,
                    enableBack: true
                });
            }
        },
        goUp () {
            this.changeStep('up');
        }
    }
};
</script>

<style lang="scss" scoped>
	.wrap-info {
		display: flex;
		justify-content: center;
		align-items: center;
	}
</style>
